import { Component, Host, Event, EventEmitter, State, Prop, h, Fragment } from '@stencil/core';
import { TranslateService } from '../../services/translate.service';
import { StateService } from '../../services/state.service';

@Component({
  tag: 'view-report-link',
  styleUrl: 'view-report-link.scss',
  shadow: true,
})
export class ViewReportLink {
  @Event() viewReportLinkClicked: EventEmitter;
  @Prop() checkingList: string[];
  @State() langTasks: any;
  private stateService: StateService = StateService.getService();

  componentWillLoad() {
    return Promise.resolve(true)
    // .then(() => {
    //   return TranslateService.getService().getTranslate();
    // })
    .then(() => {
      const res = TranslateService.getService().getTranslateSync();
      const langTasks = res?.customPrint.recentTasks;
      this.stateService.subscribe('locale-data-changed', (data) => {
        this.langTasks = data?.customPrint.recentTasks;;
      });
      this.langTasks = langTasks;
    })
  }

  private linkClicked() {
    this.viewReportLinkClicked.emit();
  }
  render() {
    return (
      <Host>
        <div class="banner">        
        {/* <calcite-action onClick={() => this.linkClicked()} text={`${this.langTasks?.label} ${this.checkingList?.length || ''}`} icon="chevrons-right" text-enabled></calcite-action> */}
        <calcite-action onClick={() => this.linkClicked()} icon="chevrons-right" text-enabled>
          <span>{this.langTasks?.label}</span>
          {this.checkingList?.length ? 
          <Fragment>
            <div class="task-num" id='task-num'>
            <span>{`${this.checkingList.length || ''}`}</span>
            </div>

            <calcite-tooltip label={this.langTasks.panelNumberTip} reference-element="task-num">
            <span>{this.langTasks.panelNumberTip}</span>
            </calcite-tooltip>
            </Fragment>
            : null
          }
        </calcite-action>

        {/* <calcite-action onClick={() => this.linkClicked()} text={this.langTasks?.label} icon="chevrons-right" text-enabled></calcite-action> */}
        </div> 
        <slot></slot>
      </Host>
    );
  }

}
