:host {
  padding: 0 0.75rem;
  $color-green-forest: #31872E;
  $color-white: #ffffff;
  div.banner {
    display: block;
    cursor: pointer;
    /* padding: 0 20px; */
    /* font-weight: 700; */
    line-height: 60px;

  }
  .task-num {
    display: inline-block;
    height: 16px;
    text-align: center;
    border-radius: 16px;
    // font-size: $font-size1;
    border-color: $color-green-forest;
    background-color: $color-green-forest;
    margin-left: 4px;
    color: $color-white;
    line-height: 16px !important;
    // padding: 1px 5px;
    transition: all .25s cubic-bezier(.18, .89, .32, 1.28);

    span {
        position: relative;
        line-height: 1 !important;
        color: $color-white;
        padding: 1px 4px;
    }

    // :host-context(html[dir='rtl']) & {
    //     margin-left: unset;
    //     margin-right: 4px;
    // }

    &.active {
        -webkit-transform: scale(1.2);
        transform: scale(1.2);
    }
  } 
}

:host-context([dir="rtl"]) {
  .task-num {
    margin-left: unset!important;
    margin-right: 4px;
  }
}

